<template>
  <div>
    <div class="lds-ring" v-if="loader"><span id="loaderText">Loading</span><div></div><div></div><div></div><div></div></div>
    <!-- Career Section End -->
    <div v-if="dataLoadSignal" style="margin-top: 82px; text-align: center;" @mouseover="expHover(true)" @mouseout="expHover(false)">
      <!-- Experience Section Start -->
      <b-container id="expSection" @mouseover="expHover(true)" @mouseout="expHover(false)" v-if="dataLoadSignal">
        <h1>Job Experience</h1>
        <div class="exp-body text-left">
          <b-row v-for="data in jobExpData" :key="data" class="exp">
            <b-col cols="12" md="3">
              <div class="exp-title">
                <h2>{{ data.name }}</h2>
                <div class="subtitle">{{ data.duration }}</div>
              </div>
            </b-col>
            <b-col cols="12" md="9">
              <div class="exp-description">
                <h2>{{ data.designation }}</h2>
                <div class="exp-details">{{ data.responsibilities }}</div>
                <ol>
                  <li v-for="item in data.resKey" :key="item"> {{ item }}</li>
                </ol>
              </div>
            </b-col>
          </b-row>
          <br>
          <h1>Educational Experience</h1>
          <div class="exp-body text-left">
            <b-row v-for="data in eduExpData" :key="data" class="exp">
              <b-col cols="12" md="3">
                <div class="exp-title">
                  <h2>{{ data.name }}</h2>
                  <div class="subtitle">{{ data.duration }}</div>
                </div>
              </b-col>
              <b-col cols="12" md="9">
                <div class="exp-description">
                  <h2>{{ data.subject }}</h2>
                  <div class="exp-details" v-for="item in data.details" :key="item">{{ item }}</div>
                </div>
              </b-col>
            </b-row>
          </div>
        </div>
      </b-container>
      <!-- Experience Section End -->
    </div>
  </div>
</template>

<script>
import { db } from '../firebase.js';
export default {
  data() {
    return {
      jobRetriveData: [],
      loader: true,
      dataLoadSignal: false,
      jobExpData: [
        { name: 'W3 Engineers Ltd.', designation: 'Jr. Software Engineer' , duration: '3 Sep 2018 - 25 Aug 2019', responsibilities: 'As a web developer, My primary responsiblities is for implementing visual and interactive components in vue. I also responsible for server-side application logic and integration of the work front-end developers do.', resKey: ['Design, Develop and Implement Vue Component.', 'Work with rest API.']},
        { name: 'Funush Private Ltd.', designation: 'Front End Developer' , duration: '1 Sep 2019 - 25 Oct 2019', responsibilities: 'As a front-end web developer, My primary responsiblities is for implementing structure, data, design, content, and functionality. I also responsible for design web pages and build different vue components.', resKey: ['Design web pages.', 'Design, Develop and Implement Vue Component.', 'Work with rest API.']}
      ],
      eduExpData: [
        { name: 'SSC', subject: 'Science' , duration: '2009 - 2011', details:['Adarsha Secondary School','Bagerhat','GPA : 5.00', 'Medium of instruction, Bangali']},
        { name: 'HSC', subject: 'Science' , duration: '2011 - 2013', details:['Government P.C. College','Bagerhat','GPA : 5.00', 'Medium of instruction, Bangali']},
        { name: 'BSc.', subject: 'Computer Science and Engineering' , duration: '2013 - 2017', details:['Jashore University of Science and Technology','Jessore','CGPA : 3.00', 'Medium of instruction, English']}
      ]
    }
  },
  firestore(){
    return {
      jobRetriveData: db.collection('jobs').orderBy('createdAt','asc')
    }
  },
  methods: {
    expHover: function(value){
      if(value == true){
        this.$emit('exp', true);
      }
      if(value == false){
        this.$emit('exp', false);
      }
    }
  },
  watch: {
    'jobRetriveData': function(){
      if(this.jobRetriveData.length > 1){
        this.loader = false;
        this.dataLoadSignal = true;
      }
    }
  }
}
</script>
<style scoped>

</style>



